// MindMap.js
import React, { useEffect, useRef } from 'react';
import { Graph } from '@antv/x6';
import Hierarchy from '@antv/hierarchy';

/* -------------- 1.  -------------- */
const COLOR_POOL = [
  '#E8F5FF', '#FFF0E8', '#F0FFE8', '#F8E8FF', '#E8FFFF', '#FFE8F0', '#FFFFE8',
];

/* -------------- 2.  + = -------------- */
Graph.registerNode(
  'topic',
  {
    inherit: 'rect',
    markup: [
      { tagName: 'rect', selector: 'body' },
      { tagName: 'text', selector: 'label' },
    ],
    attrs: {
      body: {
        rx: 12, ry: 12,
        strokeWidth: 1,
        //  setAttrs 
      },
      label: {
        fontSize: 16,
        fill: '#262626',
        whiteSpace: 'pre',
        textWrap: { width: '90%', height: '90%' },
      },
    },
  },
  true
);

Graph.registerNode(
  'topic-child',
  {
    inherit: 'rect',
    markup: [
      { tagName: 'rect', selector: 'body' },
      { tagName: 'text', selector: 'label' },
      { tagName: 'path', selector: 'line' },
    ],
    attrs: {
      body: {
        rx: 12, ry: 12,
        strokeWidth: 1,
      },
      label: {
        fontSize: 15,
        fill: '#262626',
        whiteSpace: 'pre',
        textVerticalAnchor: 'bottom',
        textWrap: { width: '90%', height: '90%' },
      },
      line: { stroke: '#d0d0d0', strokeWidth: 2, d: 'M 0 15 L 60 15' },
    },
  },
  true
);

/* -------------- 3.  -------------- */
Graph.registerEdge(
  'smooth-edge',
  {
    inherit: 'edge',
    connector: { name: 'smooth' },
    attrs: { line: { stroke: '#d0d0d0', strokeWidth: 2, targetMarker: null } },
    zIndex: 0,
  },
  true
);

/* -------------- 4.  -------------- */
const treeData = {
  text: '',
  children: [
    {
      text: '',
      children: [{ text: '' }, { text: '' }],
    },
    {
      text: '',
      children: [
        {
          text: '',
          children: [
            {
              text: '',
              children: [{ text: '' }, { text: '' }],
            },
            {
              text: '',
              children: [{ text: '' }, { text: '' }],
            },
            {
              text: '',
              children: [{ text: '' }],
            },
          ],
        },
        {
          text: '',
          children: [
            {
              text: '',
              children: [{ text: '' }, { text: '' }],
            },
          ],
        },
      ],
    },
  ],
};

/* -------------- 5. 7  +  -------------- */
function calcSize(label) {
  const col = 7;
  const lines = [];
  for (let i = 0; i < label.length; i += col) lines.push(label.slice(i, i + col));
  const newLabel = lines.join('\n');
  const baseW = 120, baseH = 40, stepW = 20, stepH = 20;
  return {
    label: newLabel,
    width: Math.max(baseW, baseW + (Math.max(...lines.map((l) => l.length)) - 4) * stepW),
    height: baseH + (lines.length - 1) * stepH,
  };
}

let idSeq = 1, colorIndex = 0;
function build(node, parentId) {
  const id = parentId ? `${parentId}-${idSeq++}` : '1';
  const { label: newLabel, width, height } = calcSize(node.text);
  const color = COLOR_POOL[colorIndex % COLOR_POOL.length];
  colorIndex += 1;

  const res = {
    id,
    type: 'topic-branch',
    label: newLabel,
    width,
    height,
    color,
  };
  if (node.children && node.children.length) {
    idSeq = 1;
    res.children = node.children.map((child) => build(child, id));
  }
  return res;
}
const initData = build(treeData);

/* -------------- 6.  75%  -------------- */
export default function MindMap() {
  const containerRef = useRef(null);

  useEffect(() => {
    if (!containerRef.current) return;

    const graph = new Graph({
      container: containerRef.current,
      width: window.innerWidth,
      height: window.innerHeight,
      background: { color: '#fafafa' },
      interacting: false,
      selecting: { enabled: false },
      panning: { enabled: true, eventTypes: ['leftMouseDown', 'touchDrag'] },
      mousewheel: {
        enabled: true,
        modifiers: [],
        factor: 1.05,
        minScale: 0.75,
        maxScale: 3,
      },
      autoResize: true,
      grid: { visible: false },
      scaling: { min: 0.75, max: 3 },
    });

    const hierarchy = Hierarchy.mindmap(initData, {
      direction: 'H',
      getHeight: (d) => d.height,
      getWidth: (d) => d.width,
      getHGap: () => 50,
      getVGap: () => 30,
      getSide: () => 'right',
    });

    const cells = [];
    const traverse = (item) => {
      const { data, children, x, y } = item;
      const node = graph.createNode({
        id: data.id,
        shape: data.type === 'topic-child' ? 'topic-child' : 'topic',
        x, y, width: data.width, height: data.height,
        label: data.label,
      });
      /*  */
      node.setAttrs({ body: { fill: data.color, stroke: data.color } });
      cells.push(node);

      children?.forEach((child) => {
        cells.push(
          graph.createEdge({
            shape: 'smooth-edge',
            source: { cell: data.id, anchor: { name: 'right' } },
            target: { cell: child.id, anchor: { name: 'left' } },
          })
        );
        traverse(child);
      });
    };
    traverse(hierarchy);
    graph.resetCells(cells);

    /*  75%  */
    graph.scale(0.75);
    graph.centerContent();

    /*  */
    const prevent = (e) => e.preventDefault();
    document.addEventListener('gesturestart', prevent, { passive: false });
    document.addEventListener('gesturechange', prevent, { passive: false });

    return () => {
      graph.dispose();
      document.removeEventListener('gesturestart', prevent);
      document.removeEventListener('gesturechange', prevent);
    };
  }, []);

  return (
    <div
      ref={containerRef}
      style={{ width: '100vw', height: '100vh', overflow: 'hidden' }}
    />
  );
}